import React, { useEffect } from "react";
import logo from "./logo.svg";
import "./App.scss";
import { Route, Switch, Redirect } from "react-router-dom";
import Nav from "./components/Nav/Nav";
import TopNav from "./components/TopNav/TopNav";
import Dashboard from "./views/Dashboard/Dashboard";
import Payers from "./views/Payers/Payers";
import Login from "./views/Login/Login";
import Orders from "./views/Orders/Orders";
import Stock from "./views/Stock/Stock";
import { connect } from "react-redux";
import { isLoggedIn } from "./store/actions/generalActions";
function App({ isAuth, isLoggedIn }) {
  useEffect(() => {
    const isLogged = localStorage.getItem("token");
    if (isLogged) isLoggedIn();
  }, [isAuth]);
  return (
    <div className='App'>
      {!isAuth ? <Redirect to='/login' /> : <Redirect to='/stock' />}
      <Route exact path='/login' component={Login} />

      {isAuth ? (
        <>
          <Nav />
          <TopNav />

          <div className='view-container'>
            <Switch>
              <Route exact path='/dashboard' component={Dashboard} />
              <Route exact path='/payers' component={Payers} />
              <Route exact path='/orders' component={Orders} />
              <Route exact path='/stock' component={Stock} />
            </Switch>
          </div>
        </>
      ) : null}
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    isAuth: state.isAuth,
  };
};

export default connect(mapStateToProps, { isLoggedIn })(App);
