import React, { useState } from "react";
import "./Login.scss";
import Logo from "../../assets/logo.jpg";
import { connect } from "react-redux";
import { login } from "../../store/actions/generalActions";

const Login = ({ login, error, history }) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  console.log(email, password);
  return (
    <div className='login'>
      <div className='login-container'>
        <div className='login-container_logo'>
          <img src={Logo} alt='Dirty Demon Logo' />
        </div>
        <h1>DIRTY DEMON</h1>
        <input type='text' name='user' placeholder='Ingresa tu email' className='login-container_input' onChange={(e) => setEmail(e.target.value)} />
        <input type='password' name='user' placeholder='Ingresa tu contraseña' className='login-container_input' onChange={(e) => setPassword(e.target.value)} />
        <button type='button' className='login-container_button' onClick={() => login({ email: email, password: password }, history)}>
          LOGIN
        </button>
        {error ? <h4 className='error'>{error}</h4> : null}
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    error: state.error,
  };
};

export default connect(mapStateToProps, { login })(Login);
