import React, { useState } from "react";
import "./StockItem.scss";
import Button from "../../Button/Button";
import { cloneDeep } from "lodash"; // Import the entire lodash library
import { updateStock } from "../../../store/actions/generalActions";
import { connect } from "react-redux";

const Item = ({ itemstock, index, newStockHandler }) => {
  return (
    <div className='itemstock'>
      <h4>{itemstock.size.toUpperCase()}</h4>
      <input type='number' min='0' name={itemstock.size} defaultValue={itemstock.stock} className='stock-item_input' onChange={(e) => newStockHandler(e.target.value, itemstock._id, index)} />
    </div>
  );
};

const StockItem = ({ item, updateStock }) => {
  const [showBody, setShowBody] = useState(false);
  const [itemValues, setItemValues] = useState(cloneDeep(item));
  const [newStock, setNewStock] = useState(cloneDeep(item.stock));

  const cancelHandler = () => {
    setItemValues(cloneDeep(item));
    setNewStock(cloneDeep(item.stock));
    setShowBody(!showBody);
  };

  const updateHandler = () => {
    const toSendValues = {
      ...itemValues,
      stock: newStock,
    };
    updateStock(toSendValues, toSendValues._id);
    setShowBody(!showBody);
  };

  const infoUpdateHandler = (value, type) => {
    const newItemValues = { ...itemValues };
    newItemValues[type] = value;
    setItemValues(newItemValues);
  };

  const newStockHandler = (value, id, index) => {
    const updateStock = [...newStock];

    const stockItem = updateStock.find((item) => {
      return item._id === id;
    });

    stockItem.stock = value;

    updateStock.slice(index, 1, stockItem);

    setNewStock(updateStock);
  };

  let stockTotal = item.stock.reduce((val, stock) => {
    return val + stock.stock;
  }, 0);

  console.log(itemValues);

  return (
    <div className='stock-item'>
      <div className='stock-item__header' onClick={() => setShowBody(!showBody)}>
        <img src={item.itemPicture.front} alt='image' />
        <h2>{item.name}</h2>
        <h2>${item.price}</h2>
        <h2>Stock: {stockTotal}</h2>
      </div>

      {showBody ? (
        <div className={`stock-item__body ${showBody ? "show" : "hidden"}`}>
          <article className='stock-item__body-container'>
            <section className='stock-item__body--left'>
              <div className='stock-item__body-item'>
                <h3>Nombre</h3>
                <input type='text' name='name' defaultValue={itemValues.name} className='stock-item_input' onChange={(e) => infoUpdateHandler(e.target.value, "name")} />
              </div>
              <div className='stock-item__body-item'>
                <h3>Precio</h3>
                <input type='text' name='price' defaultValue={itemValues.price} className='stock-item_input' onChange={(e) => infoUpdateHandler(Number(e.target.value), "price")} />
              </div>
              <div className='stock-item__body-item'>
                <h3>Descripcion</h3>
                <input type='text' name='description' defaultValue={itemValues.description} className='stock-item_input' onChange={(e) => infoUpdateHandler([e.target.value], "description")} />
              </div>
            </section>
            <section className='stock-item__body--right'>
              <div className='stock-item__body-item'>
                <h3>Stock</h3>
                {newStock.map((itemstock, index) => {
                  return <Item itemstock={itemstock} index={index} newStockHandler={newStockHandler} />;
                })}
              </div>
            </section>
          </article>
          <div className='buttons'>
            <Button onClick={cancelHandler} secondary style={{ width: "40%" }}>
              CANCELAR
            </Button>
            <Button onClick={updateHandler} style={{ width: "40%" }}>
              ACTUALIZAR
            </Button>
          </div>
        </div>
      ) : null}
    </div>
  );
};

export default connect(null, { updateStock })(StockItem);
