import React, { useEffect } from "react";
import "./Nav.scss";
import { Link } from "react-router-dom";
import { useLocation } from "react-router-dom";
import { connect } from "react-redux";
import { burgerHandler } from "../../store/actions/generalActions";

const Nav = ({ openBurger, burgerHandler }) => {
  const location = useLocation();
  console.log(location.pathname);
  console.log("open", openBurger);

  useEffect(() => {
    burgerHandler(false);
  }, [location]);

  return (
    <>
      <div className={`overlay ${openBurger ? "open" : ""}`} onClick={() => burgerHandler(false)}></div>
      <nav className={`nav ${openBurger ? "nav-open-responsive" : ""}`}>
        <div className='nav-info'></div>
        <div className='nav-content'>
          <div className='nav-content-logo'>
            <h1 className='title'>DIRTY DEMON</h1>
          </div>
          <div className='nav-content-list'>
            <div className={`nav-content-list--item ${location.pathname.includes("dashboard") ? "selected" : ""}`}>
              <Link to='/dashboard'>DASHBOARD</Link>
            </div>
            <div className={`nav-content-list--item ${location.pathname.includes("orders") ? "selected" : ""}`}>
              <Link to='/orders'>ORDENES</Link>
            </div>
            <div className={`nav-content-list--item ${location.pathname.includes("stock") ? "selected" : ""}`}>
              <Link to='/stock'>STOCK</Link>
            </div>
            <div className={`nav-content-list--item ${location.pathname.includes("payers") ? "selected" : ""}`}>
              <Link to='/payers'>COMPRADORES</Link>
            </div>
            <div className={`nav-content-list--item ${location.pathname.includes("discount") ? "selected" : ""}`}>
              <Link to='/discount'>DESCUENTOS</Link>
            </div>
          </div>
        </div>
      </nav>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    openBurger: state.openBurger,
  };
};

export default connect(mapStateToProps, { burgerHandler })(Nav);
