import React from "react";
import "./TopNav.scss";
import { burgerHandler } from "../../store/actions/generalActions";
import { connect } from "react-redux";
import { ReactComponent as Burger } from "../../assets/open-menu.svg";
const TopNav = ({ burgerHandler }) => {
  return (
    <div className='top-nav'>
      <Burger onClick={() => burgerHandler(true)} />
    </div>
  );
};

export default connect(null, { burgerHandler })(TopNav);
