import React, { useState } from "react";
import "./PayersTable.scss";
import { connect } from "react-redux";

const PayersBodyRow = ({ payerInfo }) => {
  const [expand, isExpanded] = useState(false);

  const { payer, items } = payerInfo;

  const dateArray = payer.date_created.split("T")[0].split("-");
  const date = `${dateArray[2]}/${dateArray[1]}/${dateArray[0]}`;

  const name = `${payer.name} ${payer.surname}`;

  const total = items.reduce((a, item) => {
    return a + item.price * item.quantity;
  }, 0);
  return (
    <>
      <tr className={`table_body-tr ${expand ? "expanded" : ""}`}>
        <td>{name}</td>
        <td>{date}</td>
        <td>$ {total}</td>
        <td onClick={() => isExpanded(!expand)}>+</td>
      </tr>
      {expand ? (
        <tr className='table_body-tr--expandible'>
          <td colspan='4'>
            <h5>
              Email: <span>{payer.email}</span>
            </h5>
            <h5>
              Telefono: <span>{`${payer.phone.area_code} ${payer.phone.number}`}</span>
            </h5>
            <div className='table_body-tr--expandible__buy'>
              <h5>Compra:</h5>
              <div className='item-container'>
                {items &&
                  items.map((item) => {
                    return (
                      <div className='item'>
                        <span className='item-name'>&nbsp; {item.name} &nbsp; &nbsp;</span> | <span className='item-size'>&nbsp; &nbsp; TALLE: {item.size} &nbsp; &nbsp;</span> | <span className='item-cant'>&nbsp; &nbsp; CANT: {item.quantity} &nbsp; &nbsp;</span>
                      </div>
                    );
                  })}
              </div>
            </div>
          </td>
        </tr>
      ) : null}
    </>
  );
};

const PayersTable = ({ payers }) => {
  console.log(payers);
  return (
    <div className='card'>
      <div className='card-header'>
        <h2>Compradores</h2>
      </div>
      <div className='card-body'>
        <table className='table'>
          <thead className='table_head'>
            <tr className='table_head-tr'>
              <th>Nombre</th>
              <th>Fecha</th>
              <th>Total</th>
              <th>Expandir</th>
            </tr>
          </thead>
          <tbody className='table_body'>
            {payers &&
              payers.map((payer) => {
                return <PayersBodyRow payerInfo={payer} />;
              })}
          </tbody>
        </table>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    payers: state.payers,
  };
};
export default connect(mapStateToProps)(PayersTable);
