import React, { useState } from "react";
import "./OrdersItem.scss";

const Delivery = ({ shipments }) => {
  const ultimaModArray = shipments.last_modified.split("T")[0].split("-");
  const ultimaMod = `${ultimaModArray[2]}/${ultimaModArray[1]}/${ultimaModArray[0]}`;

  const deliveryArray = shipments.shipping_option.estimated_delivery.date.split("T")[0].split("-");
  const delivery = `${deliveryArray[2]}/${deliveryArray[1]}/${deliveryArray[0]}`;

  const direccion = `${shipments.receiver_address.address_line} ${shipments.receiver_address.zip_code}`;
  const contactPhone = shipments.receiver_address.phone;
  const contactName = shipments.receiver_address.contact;
  const shippingPrice = shipments.shipping_option.price;
  const shippingName = shipments.shipping_option.name;
  const deliveryStatus = shipments.status;

  return (
    <div className='shipping-container'>
      <h3>
        Ultima Mod: <span>{ultimaMod}</span>
      </h3>
      <h3>
        Direccion: <span>{direccion}</span>
      </h3>
      <h3>
        Contacto:{" "}
        <span>
          {contactName} {contactPhone}
        </span>
      </h3>
      <h3>
        Estado: <span>{deliveryStatus}</span>
      </h3>
      <h3>
        Entrega Estimada: <span>{delivery}</span>
      </h3>
    </div>
  );
};

const OrdersItem = ({ order }) => {
  const [showBody, setShowBody] = useState(false);
  const dateToFormat = order.date_created ? order.date_created : order.payments[0].date_created;
  const dateArray = dateToFormat.split("T")[0].split("-");
  const date = `${dateArray[2]}/${dateArray[1]}/${dateArray[0]}`;

  const name = order.payer ? `${order.payer.first_name || "Sin"} ${order.payer.last_name || "Nombre"}` : "Sin Nombre";
  const email = order.payer.email;

  const total = order.payments[0].transaction_amount;

  const shipments = order.shipments[0];

  console.log("SHIPMENT", shipments);

  let status = "";
  if (order.order_status === "paid") status = "PAGO";
  else if (order.order_status === "payment_in_process") status = "EN PROCESO";
  return (
    <div className='order-item'>
      <div className='order-item__header' onClick={() => setShowBody(!showBody)}>
        <h3>{date}</h3>
        <h3>{name}</h3>
        <h3>${total}</h3>
        <h3 className={status === "PAGO" ? "order-item__header-paid" : "order-item__header-process"}>{status}</h3>
      </div>

      <div className={`order-item__body ${showBody ? "show" : "hidden"}`}>
        <article className='order-item__body-container'>
          <section className='order-item__body--right'>
            <h2>INFO</h2>
            <h3>
              Email: <span>{order.payer.email}</span>
            </h3>
            <h3>
              Nombre: <span>{name}</span>
            </h3>

            <h2 style={{ marginTop: "10px" }}>ENVIO</h2>
            {shipments ? (
              <Delivery shipments={shipments} />
            ) : (
              <h3>
                <span>Por Acordar</span>
              </h3>
            )}
          </section>
          <section className='order-item__body--left'>
            <h2>COMPRA:</h2>
            {order.buyed &&
              order.buyed.map((item) => {
                console.log(item);
                return (
                  <div className='order-container'>
                    <h3>{item ? item.name : ""}</h3>
                    <h3>TALLE: {item ? item.size.toUpperCase() : ""}</h3>
                    <h3>CANTIDAD: {item ? item.quantity : ""}</h3>
                  </div>
                );
              })}
          </section>
        </article>
      </div>

      {/* <h3>Email: {email}</h3>
      <h3>Total: ${total}</h3>
      <div>
      
      </div> */}
    </div>
  );
};

export default OrdersItem;
