import React, { useEffect } from "react";
import StockContainer from "../../components/Stock/StockContainer/StockContainer";
import { connect } from "react-redux";
import { getItems } from "../../store/actions/generalActions";
const Stock = ({ getItems }) => {
  useEffect(() => {
    getItems();
  }, []);

  return (
    <div>
      <h1 className='view-title'>STOCK</h1>
      <StockContainer />
    </div>
  );
};

export default connect(null, { getItems })(Stock);
