import * as actionTypes from "../actions/types";
const INITIAL_STATE = {
  payers: null,
  isAuth: false,
  error: null,
  items: null,
  orders: null,
  openBurger: false,
};

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case actionTypes.SET_LOGIN: {
      return {
        ...state,
        isAuth: true,
        error: null,
      };
    }
    case actionTypes.SET_ERROR: {
      return {
        ...state,
        error: action.payload,
      };
    }
    case actionTypes.GET_PAYERS_LIST: {
      return {
        ...state,
        payers: action.payload.reverse(),
      };
    }
    case actionTypes.GET_ITEMS_LIST: {
      return {
        ...state,
        items: action.payload,
      };
    }
    case actionTypes.GET_ORDERS_LIST: {
      return {
        ...state,
        orders: action.payload.reverse(),
      };
    }
    case actionTypes.SET_BURGER: {
      return {
        ...state,
        openBurger: action.payload,
      };
    }
    default:
      return state;
  }
};
