import React, { useEffect } from "react";
import PayersTable from "../../components/PayersTable/PayersTable";
import { connect } from "react-redux";
import { getPayersList } from "../../store/actions/generalActions";
const Payers = ({ getPayersList }) => {
  useEffect(() => {
    getPayersList();
  }, []);

  return (
    <div>
      <h1 className='view-title'>COMPRADORES (VIEJO)</h1>
      <PayersTable />
    </div>
  );
};

export default connect(null, { getPayersList })(Payers);
